<template>
<div id="calendar">
    <div class="top">
        <div class="ca-title">
            <div class="t-left" @click="close"><img width="25" src="./close.png" alt=""></div>
            <div class="t-right" @click='cancel' >取消</div>
        </div>
        <div class="date">
            <div>
                <p class="txt t-left">入住日期</p>
                <p class="time t-left">{{starTime}}</p>
                <p class="wk t-left">周{{common.weeks[wk1]}}</p>
            </div>
            <div class="total">
                <span>共{{delt}}晚</span>
            </div>
            <div>
                <p class="txt t-left">离店日期</p>
                <p class="time t-left">{{endTime}}</p>
                <p class="wk t-left">周{{common.weeks[wk2]}}</p>
            </div>
        </div>
    </div>
    <div class="flex" ref='flex'>

        <div>
            <calendar
                ref="calendar1"
                :model = '0'
                :range='calendar1.range'
                :events="calendar1.events" 
                :lunar="common.lunar" 
                :value="calendar1.value" 
                :begin="calendar1.begin" 
                :end="calendar1.end" 
                :weeks="common.weeks" 
                :months="common.months" 
                :disabled ='calendar1.disabled'
                @select="select"
                @selectMonth="selectMonth"
                @selectYear="selectYear"></calendar>
            <calendar
                ref="calendar1"
                :range='calendar1.range'
                :model = '1'
                :events="calendar1.events" 
                :lunar="common.lunar" 
                :value="calendar1.value" 
                :begin="calendar1.begin" 
                :end="calendar1.end" 
                :weeks="common.weeks" 
                :months="common.months" 
                @select="select"
                @selectMonth="selectMonth"
                @selectYear="selectYear"></calendar>
            <calendar
                ref="calendar1"
                :range='calendar1.range'
                :model = '2'
                :events="calendar1.events" 
                :lunar="common.lunar" 
                :value="calendar1.value" 
                :begin="calendar1.begin" 
                :end="calendar1.end" 
                :weeks="common.weeks" 
                :months="common.months" 
                @select="select"
                @selectMonth="selectMonth"
                @selectYear="selectYear"></calendar>
        </div>
    </div>
    <div class="btn" :class="btnac?'ac':''" @click='confirm'>{{btntxt}}</div>
</div>
</template>

<script>
 
import calendar from './calendar.vue'
export default {
    name: 'app',
    components: {
        calendar
    },
    props:{
        type:{
            type:String,
            defalut:''
        },
        begin:{
           type:Array,
           defalut:[] 
        },
        end:{
           type:Array,
           defalut:[] 
        }
    },
    data(){
        return {
            btntxt:'请选择入住日期',
            btnac:false,
            delt:'',
            wk1:'',
            wk2:'',
            starTime:'',
            endTime:'',
            common:{
                lunar:true, //显示农历
                weeks:['日', '一', '二', '三', '四', '五', '六'],
                months:['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
                timestamp:Date.now()
            },
            calendar1:{
                range:false,
                // begin:this.begin,
                // end:this.end,
                // range:false,
                // lunar:false,
                disabled:this.disabledDate(),
                value:[], //默认日期
                
            }
           
        }
    },
    methods:{
        confirm(){
            if(this.btntxt=='确认选择'){
                this.$emit('select',{starTime:this.starTime,endTime:this.endTime,bool:false})
            }
        },
        disabledDate(){
            let temp = [];
            let now = new Date();
            this.year = now.getFullYear()
            this.month = now.getMonth()+1
            this.day = now.getDate()
            for(var i=1;i<this.day;i++){
                var arr = []
                arr.push(this.year,this.month,i)
                temp.push(arr)
            }
            return temp
        },
       close(){
        console.log('s')
        this.$emit('close',{bool:false})
       },
       cancel(){
        let that = this
        that.starTime = that.today().toString().split(',').join('-')
        that.endTime = that.tommorrow().toString().split(',').join('-')
        this.deltDate(that.starTime,that.endTime)
        this.btntxt = '请选择入住日期'
        this.btnac = false
        console.log(that.today().toString().split(','))
        this.calendar1.value = that.today().toString().split(',')
       },
       select(value){
        console.log(value)
        if(this.btntxt == '请选择入住日期'){
            this.starTime = value.toString().split(',').join('-')
            this.wk1 = new Date(this.starTime).getDay()
            this.btntxt = '请选择离店日期'
            console.log('s')
        }else{
            if(this.btntxt = '确认选择'){
                this.starTime = value.toString().split(',').join('-')
                if(new Date(this.starTime)>new Date(this.endTime)){
                    var temp = ''
                    temp = this.starTime 
                    this.starTime = this.endTime
                    this.endTime = temp  
                }
            }else{
                this.endTime = value.toString().split(',').join('-')
                if(new Date(this.starTime)>new Date(this.endTime)){
                    var temp = ''
                    temp = this.starTime 
                    this.starTime = this.endTime
                    this.endTime = temp  
                } 
            }
            this.deltDate(this.starTime, this.endTime)
            this.wk2 = new Date(this.endTime).getDay()
            this.btntxt = '确认选择'
            this.btnac = true
            
        }
        
        
            // this.$emit('select',{value:value,type:this.type})
            // console.log(value.toString());
        },
        selectMonth(month,year){
            console.log(year,month)
        },
        selectYear(year){
            console.log(year)
        },
        today(){
            let now = new Date();
            this.year = now.getFullYear()
            this.month = now.getMonth()+1
            this.day = now.getDate()<10?('0'+now.getDate()):now.getDate()
            this.wk1 = now.getDay()
            return this.year+','+this.month+','+this.day
        },
        tommorrow(){
            let now = new Date();
            now.setTime(now.getTime()+24*60*60*1000)
            this.year = now.getFullYear()
            this.month = now.getMonth()+1
            this.day = now.getDate()<10?('0'+now.getDate()):now.getDate()
            this.wk2 = now.getDay()
            return this.year+','+this.month+','+this.day
        },
        deltDate(t1,t2){
            var det = Math.abs(Math.floor((new Date(t1) - new Date(t2))/(24*60*60*1000)))
            this.delt = det
        }
        
    },
    mounted(){
        // console.log(this.disabled)
        let that = this
        that.starTime = that.today().toString().split(',').join('-')
        that.endTime = that.tommorrow().toString().split(',').join('-')
        this.deltDate(that.starTime,that.endTime)
    }
}
</script>

<style>
/*demo*/
.btn{
    position: fixed;
    width: 80%;
    height: 0.5rem;
    line-height: 0.5rem;
    background: #dedede;
    color:#fff;
    left: 10%;
    bottom: 0.2rem;
    border-radius: 25px;
    font-size: 0.20rem;
}
.btn.ac{
    background: #fa8c1d
}
.top{
    width: 100%;
    height: 1.8rem;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1;
    background: #fff;
    box-shadow: 0 0 0.5rem #dedede;
}
.total{
    padding:0 0.1rem;
    color:#fa8c1d;

}
.total span{
    display: inline-block;
    margin-top:0.4rem;
    padding-bottom: 0.1rem;
    width: 100%;
    border-bottom: 1px solid #fa8c1d;
    font-size: 0.14rem
}
.date{
    display: flex;
    padding:0.15rem;
}
.date div{
    flex:1;
    font-size: 0.18rem;
}
.date div .txt{
    height: 0.3rem;
    line-height: 0.3rem
}
.date div .wk{
    padding: 0.1rem 0
}
.date div .time{
    font-size:  0.20rem;
    padding:0.1rem 0;
    font-weight: bold
}
.ca-title{
    height: 0.5rem;
    line-height:  0.5rem;
    width:  100%;
    font-size: 0.18rem;
   
    display: flex;

}
.ca-title div{
    flex: 1;
    padding:0 0.15rem;
}
.ca-title div img{
    margin-top:12px;
}
.t-left{
    text-align: left
}
.t-right{
    text-align: right
}
.flex{
    margin-top: 1.8rem;
    overflow-y: auto;
    box-sizing: border-box;
    display: -webkit-box;
    -webkit-box-pack: start;
    -webkit-box-align: start;
    display: -webkit-flex;
    -webkit-justify-content: space-between;
    -webkit-align-items: top;
    display: flex;
    justify-content: space-between;
    align-items: top;
    flex-flow:row wrap;
    padding-bottom: 0.6rem
}
.flex>div{
    margin:15px;
    width:100%;
    border-radius: 2px;
    position: relative;
}
.flex>div>span{
    position: absolute;
    left:0px;
    top:0px;
    padding:5px 10px;
    font-family: "PingFang SC","Hiragino Sans GB","STHeiti","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
    font-size:10px;
    border-radius:0 0 2px 0;
    background:#ea6151;
    color:#fff;
}
.flex>div>input{
    box-sizing: border-box;
    width:100%;
    margin-top:20px;
    border-radius: 2px;
    border:1px solid #dedede;
    padding:10px;
    font-size: 16px;
    background:url(data:image/svg+xml;base64,) no-repeat 8px 10px;
        padding-left: 36px;
        color:#666;
}
/*transition*/
.fade-enter-active,
.fade-leave-active {
    transition: all .5s ease-in-out;
}
.fade-enter,.fade-leave-active{
    opacity: 0;
    transform: translateY(-10px);
}
/*下拉框*/
.calendar-dropdown{
    background: #fff;
    position: absolute;
    left:0;
    top:0;
    padding:20px;
    border: 1px solid #eee;
    border-radius: 2px;
}
.calendar-dropdown:before {
    position: absolute;
    left:30px;
    top: -10px;
    content: "";
    border:5px solid rgba(0, 0, 0, 0);
    border-bottom-color: #DEDEDE;
}
.calendar-dropdown:after {
    position: absolute;
    left:30px;
    top: -9px;
    content: "";
    border:5px solid rgba(0, 0, 0, 0);
    border-bottom-color: #fff;
}
/*弹出框*/
.calendar-dialog{
    position: absolute;
    left:0;
    top:0;
    right:0;
    bottom:0;
}
.calendar-dialog-mask{
    background:rgba(255,255,255,.5);
    width:100%;
    height:100%;
}
.calendar-dialog-body{
    background: #fff;
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    padding:20px;
    border: 1px solid #eee;
    border-radius: 2px;
}
</style>


